import ListingCard from "../ListingCard/ListingCard"

const LikedHomes = ({likedHomes})=>{
    return (
        <div>
           {likedHomes.length === 0 ? <h1>No homes liked</h1>: likedHomes.map((v, i)=> <ListingCard key={likedHomes[i].id} id={likedHomes[i].id} name={likedHomes[i].name} address={likedHomes[i].address} price={likedHomes[i].price} type={likedHomes[i].type} image={likedHomes[i].images[0]} beds={likedHomes[i].beds} baths={likedHomes[i].baths}/>)}
        </div>
    );
}

export default LikedHomes;